body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(80, 77, 77);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  /* border: 2px solid red; */
  border-radius: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 30rem;
  width: 50rem;
  background-color: white;
  text-align: center;
}
.inputs{
width: 30rem;
  font-weight:bold ;
  height: 1.3rem;
  border-radius: 0.5rem;
}
.text{
  font-weight:400;
  margin: 1rem;
}


@media(max-width:1200px){
  html{
    display: block;
   }
}

@media(max-width:998px){
  html{
   display: block;
  }
  .container{
    width: 45rem;
  }
  .inputs{
    width: 35rem;
  }
  
}

@media(max-width:768px){
  html{
   display: block;
  }
  .container{
    width: 37rem;
  }
  .inputs{
    width: 27rem;
  }
  
}

@media(max-width:540px){
  html{
   display: block;
  }
  .container{
    width: 20rem;
  }
  .inputs{
    width: 15rem;
  }
  
}
@media(max-width:300px){
  html{
   display: block;
  }
  .container{
    width: 17rem;
  }
  .inputs{
    width: 12rem;
  }
  
}